import React from 'react'

function Statements() {
  return (
    <div>
      <div className="container">

      <div className="p-5 ">
        <h3 className="text-center">Our Vision</h3>
        <p>
          The James Udoekong Education Trust Fund will be a veritable source of
          financial assistance to Aka Ikot Udo Eno indigenes and its environs
          and will grow into one of the top five scholarship programmes for
          indigent students of Ibiono Ibom Local Government Area by 2030.
        </p>
      </div>

      <div className="p-5 ">
        <h3 className="text-center">Our Mission</h3>
        <p>
          The James Udoekong Education Trust Fund has been envisioned to provide
          hope, encouragement and motivation to all sons and daughters of Aka
          Ikot Udo Eno desirous to pursue higher education irrespective of their
          parental background.
        </p>
      </div>
      </div>
    </div>
  );
}

export default Statements
