import React from 'react'
import p1 from '../img/p1.jpg'
import p2 from '../img/p2.jpg'
import coin from '../img/coin.svg'
import love from '../img/love.svg'
import share from '../img/share.svg'
import baby from '../img/baby.svg'
import {Link} from 'react-router-dom'
function Home() {
  return (
    <>
    <div className="home">
      <section
        class="mission-quote section mt-5 mb-5 text-center"
        data-aos="fade-up"
        data-aos-duration="1200"
      >
        <div class="container">
          <small class="small-text text-uppercase font-weight-bold">
            our mission
          </small>
          <h5 class="mt-3 mb-4">
            Our mission is to provide a veritable source of financial assistance
            to those who need it the most.
            <span class="block-fluid-text"> </span>
          </h5>
          <div class="quote-border"></div>
        </div>
      </section>

      <section id="history-section">
        <div class="container">
          <div class="row">
            <div class="col-md-6 box">
              <div
                id="history-text"
                class="d-flex flex-column justify-content-center"
              >
                <small class="small-text pb-1 text-uppercase font-weight-bold">
                  Our history
                </small>

                <h3 class="mb-1">
                  <span class="block-text">
                    Making the academic dreams of the needy come true{" "}
                  </span>{" "}
                  since 2000
                </h3>
                <p>
                  Engr. Anthony Udoekong, a young man in his 30s who spoke to
                  announce the institution forthwith of an Education Trust Fund
                  ...in memory of his late father, a lover of education and its
                  development potentials announced an immediate donation of
                  N50,000 (Fifty Thousand Naira) which marked the beginning of a
                  scholarship programme for students of Aka Ikot Udo Eno origin
                  wherever they may be studying in tertiary institutions.{" "}
                </p>
                <Link to="/history">
                  <small class="small-text text-uppercase font-weight-bold">
                    learn more
                  </small>
                </Link>
              </div>
            </div>
            <div class="col-md-6">
              <div
                id="history-img"
                class="d-flex flex-column justify-content-center"
              >
                <img src={p1} alt="unity among races" class="image-fluid" />
              </div>
            </div>
          </div>
        </div>
      </section>
      <section id="stats" class="py-5">
        <div class="container d-flex justify-content-between text-center">
          <div class="row">
            <div class="stats-value col-sm-4 py-3">
              <h1>17,000</h1>
              <small>
                lives touched
                <br />
                per year
              </small>
            </div>
            <div class="stats-value col-sm-4 py-3">
              <h1>20,000</h1>
              <small>
                student's dreams
                <br />
                actualized
              </small>
            </div>
            <div class="stats-value col-sm-4 py-3">
              <h1>450</h1>
              <small>
                partners involved
                <br />
                in our programs
              </small>
            </div>
          </div>
        </div>
      </section>
      <section id="testimonial-section">
        <div class="container">
          <div class="row t-row">
            <div class="col-md-6 t-img-col">
              <div
                id="testimonial-img"
                class="d-flex flex-column justify-content-center"
              >
                <img
                  src={p2}
                  alt="person happy"
                  class="image-fluid m-auto mb-md-4 mb-sm-4"
                />
              </div>
            </div>

            <div class="col-md-6 t-text-box">
              <div
                id="testimonial-text"
                class="d-flex flex-column justify-content-center"
              >
                <small class="small-text pb-1 text-uppercase font-weight-bold">
                  Testimonial
                </small>

                <h3>
                  I'm so grateful to Engr. John.
                  <br />
                  What you are giving is helping out a lot families like mine
                  who wouldn't make it in this city.
                  <small class="text-uppercase mt-2">
                    Jeset, Local resident.
                  </small>
                </h3>
                {/* <a href="#">
              <small class="small-text text-uppercase font-weight-bold">read more &longrightarrow;</small>
            </a> */}
              </div>
            </div>
          </div>
        </div>
      </section>
      <section
        class="mission-quote section mt-5 mb-5 text-center"
        data-aos="fade-up"
        data-aos-duration="1200"
      >
        <div class="container">
          <small class="small-text text-uppercase font-weight-bold">
            how to help
          </small>
          <h5 class="mt-3 mb-4">
            With you support, we can do more,
            <span class="block-fluid-text">
              {" "}
              you too can help us sponsor more students via our scholarship
              scheme!
            </span>
          </h5>
          <div class="quote-border"></div>
        </div>
      </section>

      <section
        data-aos="fade-up"
        data-aos-duration="1000"
        id="info-action"
        class="container"
      >
        <div class="row text-center justify-content-center">
          <div class="card my-2 mx-2">
            <a href="/donate">
              <h1 class="icons">
                <img src={coin} alt="" />
              </h1>
              <h2 class="title">Donate</h2>
              <p>
                Lorem ipsum dolor sit, amet consectetur adipisicing elit.
                Accusamus, praesentium.
              </p>
            </a>
          </div>

          <div class="card my-2 mx-2">
            <a href="##">
              <h1 class="icons">
                <img src={baby} width="51" alt="" />
              </h1>
              <h2 class="title">Touch lives</h2>
              <p>
                Lorem ipsum dolor sit, amet consectetur adipisicing elit.
                Accusamus, praesentium.
              </p>
            </a>
          </div>

          <div class="card my-2 mx-2">
            <a href="##">
              <h1 class="icons">
                <img src={share} alt="" />
              </h1>
              <h2 class="title">Share with Friends</h2>
              <p>
                Lorem ipsum dolor sit, amet consectetur adipisicing elit.
                Accusamus, praesentium.
              </p>
            </a>
          </div>

          <div class="card my-2 mx-2">
            <a href="##">
              <h1 class="icons">
                <img src={love} alt="" />
              </h1>
              <h2 class="title">Assist</h2>
              <p>
                Lorem ipsum dolor sit, amet consectetur adipisicing elit.
                Accusamus, praesentium.
              </p>
            </a>
          </div>
        </div>
      </section>
   <section id="p-foot" class="mt-5 text-center">
    <div class="container">

      <h2>Education is vital but can be expensive.</h2>

      <p>
        The James Udoekong Education Trust Fund has been envisioned to provide hope and motivation to all sons and
        daughters of Aka Ikot Udo Eno desirous to pursue higher education and skills acquisition irrespective of their
        parental background. Also, to grow into one of the greatest scholarship programmes for indigent youths of Ibiono
        Ibom Local Government Area.

      </p>
      
    </div>

  </section>
    </div>
    </>
  );
}

export default Home
